import React from "react";
import 'react-html5-camera-photo/build/css/index.css';
import Camera from 'react-html5-camera-photo';

/*global cv*/

class DominoDotCounter extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            photo: "",
        };
    }
    count = -1;

    handleTakePhoto = (dataUri) => {
        this.setState({
            photo: dataUri,
        });
    };

    countDots = () => {
        let srcMat = cv.imread("imgSrc");
        let dstMat = srcMat.clone();

        let contours = new cv.MatVector();
        let searchableContours = new cv.MatVector();
        let dotsVec = new cv.MatVector();
        let heirarchy = new cv.Mat();

        cv.cvtColor(srcMat, srcMat, cv.COLOR_RGBA2GRAY);
        cv.GaussianBlur(srcMat, srcMat, new cv.Size(3, 3), 2, 2);
        cv.Canny(srcMat, srcMat, 25, 75, 3);

        cv.findContours(
            srcMat,
            contours,
            heirarchy,
            cv.RETR_LIST,
            cv.CHAIN_APPROX_SIMPLE
        );

        for (let i = 0; i < contours.size(); i++) {
            if (
                cv.contourArea(contours.get(i)) > 50 &&
                (

                Math.abs(cv.contourArea(contours.get(i))/cv.arcLength(contours.get(i), false) - 25) < 10)
            ) {
                searchableContours.push_back(contours.get(i));
            }
        }

        for (let i = 0; i < searchableContours.size(); i++) {
            let moment = cv.moments(searchableContours.get(i));
            let cx = moment.m10 / moment.m00;
            let cy = moment.m01 / moment.m00;

            let foundDuplicate = false;
            for (let j = i; j < searchableContours.size(); j++) {
                let moment2 = cv.moments(searchableContours.get(j));
                let cx2 = moment2.m10 / moment2.m00;
                let cy2 = moment2.m01 / moment2.m00;
                let closeX = Math.abs(cx - cx2);
                let closeY = Math.abs(cy - cy2);
                if (closeX > 0 && closeX < 10 && closeY > 0 && closeY < 10) {
                    foundDuplicate = true;
                    break;
                }
            }
            if (!foundDuplicate) {
                dotsVec.push_back(searchableContours.get(i));
            }
        }

        for (let i = 0; i < dotsVec.size(); i++) {
            cv.drawContours(dstMat, dotsVec, i, new cv.Scalar(255, 0, 0, 255), 3);
        }

        this.count = dotsVec.size();
        document.getElementById("result").innerText = this.count;

        cv.imshow("imgDst", dstMat);
    };

    render() {
        if (this.props.showResult) {
            cv.imshow("imgDst");
        }
        return (
            <div>
                <Camera isImageMirror={false} idealFacingMode={"environment"} isMaxResolution={true} onTakePhoto={(dataUri) => { this.handleTakePhoto(dataUri) }} />
                <img
                    alt="hidden"
                    id={"imgSrc"}
                    src={this.state.photo}
                    onLoad={this.countDots}
                    style={{ position: "absolute", left: "-1000%" }}
                ></img>
                <div>
                    Amount: <div id="result"></div>
                </div>
                <canvas id={"imgDst"} />
            </div>
        );
    }
}

export default DominoDotCounter;